.container__big-card{
    margin-left: 20px;
    margin-right: 20px;
    width: 400px;
    height: 500px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.big-card__title > h1{
    margin-top: 15px;
    font-size: 25px;
}

.big-card__description > p{
    margin-top: 10px;
    color: grey;
}

.big-card__photo > img{
    width: 100%;
    min-width: 385px;
}